import React from 'react';


export default class BannerTable extends React.Component {


    getTableRows = () => {
        let obj = this.props.bannerInfo.table;
        let keys = Object.keys(this.props.bannerInfo.table);

        let toReturn = keys.map((key, index) => {
            let inlineObj = obj[key];

            return (
                <tr>{this.getTableCol(inlineObj, index)}</tr>
            );
        });

        return toReturn;

    }

    getTableCol = (item, index) => {
        let keys = Object.keys(item);

        let toReturn = keys.map((key) => {
            if (index == 0) {
                return (
                    <th style={{ backgroundColor: this.props.bannerInfo.buttonColor, color: this.props.bannerInfo.textColor }}>{item[key]}</th>
                );
            } else {
                return (
                    <td>{item[key]}</td>
                );
            }

        });

        return toReturn;
    }


    render() {


        return (

            < div className="price_table" >
                <h1 style={{ color: this.props.bannerInfo.textColor }}>{this.props.bannerInfo.header}</h1>
                <table>

                    {this.getTableRows()}

                </table>
                <button onClick={() => { this.props.flipBanner(false); }} style={{ color: this.props.bannerInfo.textColor, backgroundColor: this.props.bannerInfo.buttonColor }}>BACK</button>
            </div >
        )
    };


}



