import React from 'react';



export default class BannerOrder extends React.Component {

    state = {
        inputEmail: "",
        inputMessage: "",
        inputSelect: "Small",
    }



    handleClick = (e) => {
        e.preventDefault();
        alert(`Selectet = ${this.state.inputSelect}/ Email = ${this.state.inputEmail}/ Nachricht = ${this.state.inputMessage}`)
    }

    render() {
        return (
            <div className="banner_order">
                <h1 style={{ color: this.props.bannerInfo.textColor }}>Bestellen</h1>
                <select
                    style={{ backgroundColor: this.props.bannerInfo.selectColor, color: this.props.bannerInfo.textColor }}
                    onChange={(e) => { this.setState({ inputSelect: e.target.value }); }}
                    value={this.state.inputSelect}>
                    <option value="Small">Small</option>
                    <option value="Medium">Medium</option>
                    <option value="Large">Large</option>
                </select>
                <form>
                    <input
                        type="email"
                        name="Email"
                        placeholder="Email"
                        style={{ backgroundColor: this.props.bannerInfo.inputColor, color: this.props.bannerInfo.textColor }}
                        value={this.state.inputEmail}
                        onChange={(e) => { this.setState({ inputEmail: e.target.value }); }}>
                    </input>
                </form>
                <textarea
                    rows="10"
                    cols="50"
                    placeholder="Text hier eingeben..."
                    name="comment"
                    form="usrform"
                    style={{ backgroundColor: this.props.bannerInfo.inputColor, color: this.props.bannerInfo.textColor }}
                    value={this.state.inputMessage}
                    onChange={(e) => { this.setState({ inputMessage: e.target.value }); }}>
                </textarea>
                <button
                    onClick={(e) => { this.handleClick(e); }}
                    style={{ backgroundColor: this.props.bannerInfo.buttonColor, color: this.props.bannerInfo.textColor }}>
                    Senden
                </button>
            </div>
        )
    };


}


