import React from 'react';
import BannerTable from './banner_table';






export default class BannerBack extends React.Component {


    normalState = (props) => {
        return (
            <div className="back_content" style={{ color: props.bannerInfo.textColor }}>
                <h1>{props.bannerInfo.header}</h1>
                <p>{props.bannerInfo.longDescription}</p>
                <button onClick={() => { props.flipBanner(false); }} style={{ color: props.bannerInfo.textColor, backgroundColor: props.bannerInfo.buttonColor }}>BACK</button>
            </div>
        );
    }


    selectContent = () => {
        if (this.props.bannerInfo.style == "table") {
            return (
                <BannerTable bannerInfo={this.props.bannerInfo} flipBanner={this.props.flipBanner} />
            );
        } else {
            return (
                this.normalState(this.props)
            );
        }
    }

    render() {
        return (
            <div className="banner_back" style={{ visibility: this.props.visibility ? "visible" : "hidden", backgroundColor: this.props.bannerInfo.backgroundColor }}>
                {this.selectContent(this.props)}
            </div>
        )
    };

}


